<template>
  <div>
    <!-- Toolbar -->
    <corriendito-toolbar @clickedSidebar="toggleSidebar" @clickedShoppingCart="toggleShoppingCart"></corriendito-toolbar>
    <!-- End Toolbar -->
    <corriendito-sidebar
      ref="sidebarDrawerValue"
      @clickedSidebar="toggleSidebar"
    ></corriendito-sidebar>
    <corriendito-shoppingcart
      ref="shoppingCartDrawerValue"
      @clickedShoppingCart="toggleShoppingCart"
    ></corriendito-shoppingcart>
  </div>
</template>
<script>
import Toolbar from "@/components/layout/Toolbar";
import Sidebar from "@/components/layout/NavigationSideBar";
import ShoppingCart from "@/components/layout/ShoppingCartSideBar";
// import { mapState } from "vuex";

export default {
  components: {
    CorrienditoToolbar: Toolbar,
    CorrienditoSidebar: Sidebar,
    CorrienditoShoppingcart: ShoppingCart
  },
  computed: {},
  methods: {
    toggleSidebar() {
      this.$refs.sidebarDrawerValue.sidebarDrawerValue = !this.$refs
        .sidebarDrawerValue.sidebarDrawerValue;
    },
    toggleShoppingCart() {
      this.$refs.shoppingCartDrawerValue.shoppingCartDrawerValue = !this.$refs
        .shoppingCartDrawerValue.shoppingCartDrawerValue;
    }
  }
};
</script>
