import Vue from "vue";
import Vuex from "vuex";
import VuexPersistence from "vuex-persist";

// :: Modules
import login from "@/store/login";
import products from "@/store/products";
import notifications from "@/store/user/notifications";
import orders from "@/store/user/orders";
import profile from "@/store/user/profile";
import PreguntasFrecuentes from "@/store/faqs";
import terminos from "@/store/terms";
import address from "@/store/address";
import addressList from "@/store/user/addressList";
import checkout from "@/store/checkout";

Vue.use(Vuex);

const vuexLocal = new VuexPersistence({
  supportCircular: true,
  storage: window.localStorage,
  // :: Modules that need persistence
  modules: ["login"]
});

const store = new Vuex.Store({
  modules: {
    login,
    products,
    notifications,
    orders,
    profile,
    checkout,

    PreguntasFrecuentes,
    terminos,

    address,
    addressList

  },
  plugins: [vuexLocal.plugin],
});

export default store;
