<template>
  <div>
    <v-card class="mx-auto" width="200">
      <v-list flat>
        <v-list-item v-for="(item, index) in items" :key="index" @click="navigateTo(item.to)">
          <v-list-item-icon>
            <v-icon color="primary" v-text="item.icon"></v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title v-text="item.title"></v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-card>
  </div>
</template>
<script>
import { mapActions } from "vuex";
export default {
  components: {},
  computed: {},
  data() {
    return {
      items: [
        { title: "Mis datos", to: "profile", icon: "mdi-account" }, //User profile
        { title: "Notificaciones", to: "notifications", icon: "mdi-bell" }, //notificaciones
        { title: "Mis órdenes", to: "orders", icon: "mdi-bike-fast" }, //Mis Ordenes
        { title: "Mis favoritos", to: "favorites", icon: "mdi-heart" }, //Mis Ordenes
        { title: "Cerrar Sesión", to: "logout", icon: "mdi-logout" } //notificaciones
      ]
    };
  },
  methods: {
    ...mapActions("login", ["logoutAction"]),
    navigateTo(path) {
      if (path != "") {
        if (path == "logout") {
          this.logoutAction();
        } else {
          this.$router.push({ name: path });
        }
      }
    }
  }
};
</script>
