<template>
  <v-card>
    <v-row align="center" justify="end" class="pr-4">
      <v-btn icon @click="closeAddressList">
        <v-icon dark>mdi-close-circle</v-icon>
      </v-btn>
    </v-row>
    <v-card-title>Direcciones guardadas</v-card-title>
    <v-spacer></v-spacer>
    <v-card-subtitle>
      Hola
      <span class="addressList__user_name">{{this.data.name}}</span>, ¿a dónde quieres pedir hoy?
    </v-card-subtitle>
    <v-divider></v-divider>

    <v-col v-if="!myAddressList.length">
      <v-card outlined>
        <v-card-title>
          <span class="addressList__no_addres">Actualmente no hay ninguna dirección guardada</span>
        </v-card-title>
      </v-card>
    </v-col>

    <v-card-text style="height: 300px;">
      <v-col cols="12" class="pb-0" v-for="address in myAddressList" :key="address.id">
        <v-card outlined>
          <v-radio-group v-model="addressSelectedR" column>
            <v-radio
              :value="address.id + '/' + address.address + '/' + address.latitude + '/' + address.longitude"
              :mandatory="false"
              class="mb-0"
              :key="address.id"
            >
              <template v-slot:label>
                <div>
                  <strong>{{address.address}}</strong>
                </div>
              </template>
            </v-radio>
            <v-card-title class="pt-2 pb-0">
              <span class="addressList__addres_alias">{{address.alias}}</span>
            </v-card-title>
            <v-card-text class="pt-0 pb-0 addressList__addres_references">{{address.references}}</v-card-text>
          </v-radio-group>
        </v-card>
      </v-col>
    </v-card-text>

    <v-divider></v-divider>
    <v-card-actions class="addressList__buttons">
      <v-btn class="ma-2" dark color="primary" @click="navigateToNewAddress">
        <v-icon dark right small>mdi-map-marker-plus</v-icon>Nueva dirección
      </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      addressSelectedR: "",
      addressObject: {},
      flagClosed: true
    };
  },
  async mounted() {
    await this.getUserAddressList();

    if (this.data.addressSelected != null) {
      var data = JSON.parse(JSON.stringify(this.data.addressSelected));
      this.addressSelectedR =
        this.data.addressSelected != null
          ? data.addressId +
            "/" +
            data.addressName +
            "/" +
            data.addressLatitude +
            "/" +
            data.addressLongitude
          : "";
    }
  },
  computed: {
    ...mapState("addressList", ["myAddressList"]),
    ...mapState("login", ["data"])
  },
  methods: {
    ...mapActions("addressList", ["getUserAddressList"]),
    ...mapActions("login", ["getStoresByLocation"]),
    closeAddressList() {
      this.$emit("closeAddressListDialog", false);
    },
    getIdAndNameAddress(valueAddress) {
      var adressId =
        valueAddress && valueAddress != "-undefined"
          ? valueAddress.split("/")[0]
          : this.data.addressSelected.addressId;
      var adressName =
        valueAddress && valueAddress != "-undefined"
          ? valueAddress.split("/")[1]
          : this.data.addressSelected.addressName;
      var latitude =
        valueAddress && valueAddress != "-undefined"
          ? valueAddress.split("/")[2]
          : this.data.addressSelected.latitude;
      var longitude =
        valueAddress && valueAddress != "-undefined"
          ? valueAddress.split("/")[3]
          : this.data.addressSelected.longitude;

      return {
        addressId: adressId,
        addressName: adressName,
        addressLatitude: latitude,
        addressLongitude: longitude
      };
    },
    navigateToNewAddress() {
      this.$router.push({ name: "addressRegister" });
      this.$emit("closeAddressListDialog", false);
    },
    setAddress() {
      
    }
  },
  watch: {
    addressSelectedR(val) {
      var addressObjectSelected = this.getIdAndNameAddress(val);
      this.data.addressSelected = addressObjectSelected;
      this.getStoresByLocation();
      if(this.flagClosed) {
        this.flagClosed = false
      } else {
        this.closeAddressList()
      }
      //this.$emit("closeAddressListDialog");
    }
  }
};
</script>
<style lang="scss" scoped>
.addressList {
  &__buttons {
    justify-content: center;
  }
  &__user_name {
    font-weight: bold;
  }
  &__no_addres {
    font-size: 15px;
  }
  &__addres_alias {
    font-size: 15px;
    color: var(--color-primary);
  }
  &__addres_references {
    font-size: 12px;
    text-align: justify;
    color: var(--color-grayDark);
  }
}
</style>