import axios from '@/config/axios-auth'
import { errorMessage, showMessage } from "@/services/MessageService";
import {
  GET_TERMINOS
} from "./mutation-types";

export default {
  async getTerminos({ commit }) {
 
    axios.request({
        url: '/termsAndConditions',
        method: 'get'
      }).then(response => {
        if (response) {
            commit(GET_TERMINOS, response);
        } else {
          errorMessage(response.data.message);
        }
      })
      .catch(error => errorMessage(error))
      
    }
}