<template>
  <v-overlay
    :value="progress"
    color="primary"
  >
    <v-progress-circular
      indeterminate
      size="64"
    ></v-progress-circular>
  </v-overlay>
</template>
<script>
import EventBus from "@/config/bus";

export default {
  data() {
    return {
      progress: false,
      error: false
    };
  },
  mounted() {
    EventBus.$on("PROGRESS_SHOW", () => {
      this.progress = true;
      // :: Hide after 6 seconds, in case the request fails
      setTimeout(function() {
        this.hideProgress;
      }, 6000);
    });
    EventBus.$on("PROGRESS_HIDE", () => {
      this.progress = false;
    });
  },
  methods: {
    hideProgress() {
      EventBus.$emit("PROGRESS_HIDE");
      this.error = false;
    },
    reloadPage() {
      this.$router.go();
    }
  }
};
</script>
<style lang="scss" scoped>
.loader {
  position: absolute;
  z-index: 100;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgba(99, 8, 102, 0.6);
}
</style>
