// :: Dependencies
import axios from "../../config/axios-auth";
import { loaderDone, loaderStart } from "@/services/LoaderService";
import { USER_ADDRESS_SAVE, STORAGE_COVERAGE_GET } from "./mutation-types";
import { errorMessage } from "@/services/MessageService";

export default {
  async saveUserAddress({ commit }, addressInfo) {
    loaderStart();
    try {
      const addressCreate = await axios.post("/address", {
        alias: addressInfo.alias,
        address: addressInfo.address,
        references: addressInfo.references,
        latitude: addressInfo.latitude,
        longitude: addressInfo.longitude,
      });
      if (addressCreate.data.success) {
        commit(USER_ADDRESS_SAVE, addressInfo);
      } else if (addressCreate.data.message == "No se cuenta con cobertura") {
        errorMessage("No hay cobertura en tu locación")
      }
    } catch (error) {
      errorMessage(error.code)
    } finally {
      loaderDone();
    }
  },
  async getCoverages({ commit }) {
    loaderStart();
    try {
      const storageCoverage = await axios.get("/coverages", {
        content_type: "application/x-www-form-urlencoded",
      });
      if (storageCoverage.data.success) {
        commit(STORAGE_COVERAGE_GET, storageCoverage.data.data[0].polygon);
      } else {
        errorMessage(storageCoverage.data.message )
      }
    } catch (error) {
      errorMessage(error.code);
    } finally {
      loaderDone();
    }
  },
};
