import { CHECKOUT_PROCESS, SAVE_PAYMENT_METHODS, SAVE_PICKUP_METHODS, SAVE_SUBTOTAL } from "./mutation-types";

export default {
  [CHECKOUT_PROCESS](state, dataCheckout) {
    state.data = dataCheckout;
  },
  [SAVE_PAYMENT_METHODS](state, paymentsData) {
    state.paymentMethods = paymentsData
  },
  [SAVE_SUBTOTAL](state, subtotal){
    state.subtotal = subtotal
  },
  [SAVE_PICKUP_METHODS](state, pickupData) {
    state.pickupMethods = pickupData
  }
};
