// :: Dependencies
import axios from '@/config/axios-auth'
import store from "@/store";
// import { loaderDone, loaderStart } from "@/services/LoaderService";
import { errorMessage, showMessage } from "@/services/MessageService";
import {
  DATA_SAVE
} from "./mutation-types";


export default {
  getFavorites({ commit }) {
    const storeData = store.getters['login/dataStore'];
    axios.get(`/store/${storeData.id}/favorites`)
      .then(res => {
        if (res.data.success) {

          commit(DATA_SAVE, res.data.data);
        } else {
          errorMessage(res.data.message)
        }
      })
      .catch(error => errorMessage(error))

  },
  addProductToFavorites({ dispatch }, data) {
    const storeData = store.getters['login/dataStore'];
    axios.post(`/store/${storeData.id}/business/${data.id_business}/favorites`, {
      product: data.id_product
    })
      .then(res => {
        if (res.data.success) {
          dispatch('getFavorites')
        } else {
          errorMessage(res.data.message)
        }
      })
      .catch(error => errorMessage(error))
  }
};
