import {
  PHONENUMBER_SAVE,
  CHANGE_SCREEN,
  CODE_SAVE,
  USERDATA_SAVE,
  TOKEN_SAVE,
  LOGOUT,
  USERDATA_ADDRESS_COUNTER_SET,
  UPDATE_FLAG_LOGIN,
  ADDRESS_SELECTED_SET,
  SAVE_STORE_DATA
} from "./mutation-types";

export default {
  [PHONENUMBER_SAVE](state, phoneNumber) {
    state.phoneNumber = phoneNumber
  },
  [CODE_SAVE](state, code) {
    state.verificationCode = code
  },
  [USERDATA_SAVE](state, userData){
    state.data = userData
  },
  [USERDATA_ADDRESS_COUNTER_SET](state, userData){
    state.addressCount = userData.addressCount;
    state.flagLogin = userData.flagLogin;
  },
  [UPDATE_FLAG_LOGIN](state, flagValue){
    state.flagLogin = flagValue;
  },
  [ADDRESS_SELECTED_SET](state, dataAddress){
    state.data.addressSelected = dataAddress;
    state.auxOneAddress = dataAddress.auxOneAddress;
  },
  [TOKEN_SAVE](state, dataToken) {
    state.token_user = dataToken.access_token
    state.expiration_date = dataToken.expires_in
    state.isLogged = true
  },
  [LOGOUT](state) {
    state.tokenUser = ''
    state.expiration_date = ''
    state.isLogged = false,
    state.flagLogin = false,
    state.addressCount = 0,
    state.auxOneAddress = false
    state.dataUser = null
  }, 
  [SAVE_STORE_DATA](state, dataStore){
    state.dataStore = dataStore
  }
};
