// :: Dependencies
import axios from '../../config/axios-auth'
import store from "@/store";
import router from "@/config/router";
import { loaderDone, loaderStart } from "@/services/LoaderService";
import { errorMessage, showMessage } from "@/services/MessageService";
import {
  PHONENUMBER_SAVE,
  CODE_SAVE,
  USERDATA_SAVE,
  TOKEN_SAVE,
  LOGOUT,
  USERDATA_ADDRESS_COUNTER_SET,
  UPDATE_FLAG_LOGIN,
  ADDRESS_SELECTED_SET,
  SAVE_STORE_DATA
} from "./mutation-types";
import instance from '../../config/axios-auth';


export default {
  // :: getVerificationCodeAction
  // :: Performs a send verification code 
  // :: Custom callable function /api/start
  getVerificationCodeAction({ commit }, phoneNumber) {

    axios.post('/start', {
      phone_number: "52" + phoneNumber
    })
      .then(res => {
        if (res.data.success) {
          commit(PHONENUMBER_SAVE, phoneNumber);
          commit(USERDATA_SAVE, res.data.data);
          if (res.data.message == 'Por favor verifica tu número') {
            router.push({name: 'verificationCode'});
          } else if (res.data.message == 'Ingresa tu contraseña para continuar') {
            router.push({name: 'password'});
          }
        } else {
          errorMessage(res.data.message)
        }
      })
      .catch(error => errorMessage(error))
  },
  postVerificationCodeAction({ commit }, verificationCode) {
    
    const phoneNumber = store.getters['login/phoneNumber'];
    axios.post('/verify', {
      phone_number: "52" + phoneNumber,
      verification_code: verificationCode
    })
      .then(res => {
        if (res.data.success) {
          commit(CODE_SAVE, verificationCode);
          router.push({name: 'register'});
        }  else {
          errorMessage(res.data.message)
        }
      })
      .catch(error => errorMessage(error))
  },
  loginAction({ commit }, password){
    const phoneNumber = store.getters['login/phoneNumber'];
    axios.post('/login', {
      phone_number: "52" + phoneNumber,
      password: password
    })
      .then(async res => {
        if (res.data.success) {
          instance.defaults.headers.Authorization = res.data.data.token_type + ' ' + res.data.data.access_token
          await commit(TOKEN_SAVE, res.data.data)
          showMessage(res.data.message)
          //verify if user has any address previously selected
          const userData = store.getters['login/data']; //getUserData
          await this.dispatch('login/initAddressLogin', {userData}, {root:true});
          
        } else {
          errorMessage(res.data.message)
        }
        
      })
      .catch(error => errorMessage(error))
  },
  async initAddressLogin({ commit }, userData) {
    //dispatch to call getUserAddressList function and get directions
    await this.dispatch('addressList/getUserAddressList', {}, {root:true});
    const addressList = store.getters['addressList/myAddressList']; //getUserAddressListData

    if (userData.addressSelected && userData.addressSelected.length > 0){
      router.push({name: 'home'});
    }else{
      if (addressList.length == 0) {
        router.push({ name: "addressRegister" });
      }else
      if (addressList.length == 1) {
        commit(ADDRESS_SELECTED_SET, {addressId: addressList[0].id, 
                                    addressName: addressList[0].address,
                                    addressLatitude: addressList[0].latitude,
                                    addressLongitude: addressList[0].longitude, 
                                    auxOneAddress: true});
        router.push({name: 'home'});
      }else
      if (addressList.length > 1) {
        commit(USERDATA_ADDRESS_COUNTER_SET, {addressCount:addressList.length, flagLogin: false});
        router.push({name: 'home'});
      }
    }
  },
  updateFlagLogin({ commit }, flagValue) {
    commit(UPDATE_FLAG_LOGIN, flagValue);
  },
  registerUserAction({ commit }, dataRegisterUser) {
    const { phone_number, name, email, password, verification_code } = dataRegisterUser;
    axios.post('/completeRegister', {
      phone_number: "52" + phone_number,
      name: name,
      email: email,
      password: password,
      verification_code: verification_code
    })
      .then(res => {
        if (res.data.success) {
          instance.defaults.headers.Authorization = res.data.data.token_type + ' ' + res.data.data.access_token
          commit(TOKEN_SAVE, res.data.data)
          router.push({name: 'addressRegister'});
        } else {
          errorMessage(res.data.message)
        }
      })
      .catch(error => errorMessage(error))
  },
  logoutAction({commit}){
    errorMessage("Cierre de sesión exitoso")
    commit(LOGOUT)
    delete instance.defaults.headers.common['Authorization']
    router.push({name: 'login'});
  },
  getUnauthorizedError({commit}) {
    instance.interceptors.response.use(undefined, function (err) {
        if (err.status === 401) {
          commit(LOGOUT)
        }
    });
  },
  resetPasswordAction({commit}, email) {
    //TODO ADD SERVICE BECAUSE IS NOT IN POSTMAN 
    router.push({name: 'password'});
  },
  getStoresByLocation({ commit, dispatch, getters }) {
    loaderStart()
    const data = getters.data
    axios.get(`/store/location/${data.addressSelected.addressLatitude}, ${data.addressSelected.addressLongitude}`)
      .then(res => {
        if (res.data.success) {
          this.dispatch('products/getStoresById', res.data.data.id)
          this.dispatch('products/getMissionsByStore', res.data.data.id)
          commit(SAVE_STORE_DATA, res.data.data);
        } else {
          errorMessage(res.data.message)
        }
      })
      .catch(error => errorMessage(error))
  },
};
