import {
    GET_USER_PROFILE,
    UPDATE_USER_PROFILE
} from "./mutation-types";
  
  export default {
    [GET_USER_PROFILE](state, dataProfile) {
      state.userProfile = dataProfile.data;
    },
    [UPDATE_USER_PROFILE](state, newDataProfile) {
      state.userProfile =  newDataProfile.data;
  }
  };