// :: Dependencies
import axios from "@/config/axios-auth";
import store from "@/store";
import { errorMessage, showMessage } from "@/services/MessageService";
import { loaderDone, loaderStart } from "@/services/LoaderService";
// import { loaderDone, loaderStart } from "@/services/LoaderService";
import {
  DATA_SAVE,
  CATEGORIES_SAVE,
  FEATURED_PRODUCTS,
  RESTORE_CONTENT,
  RESULTS_BUSINESS_SAVE,
  CATEGORY_PRODUCTS,
  CART_ITEMS_GET,
} from "./mutation-types";

export default {
  getStoreData({ commit }, id_store) {
    const storeData = store.getters['login/dataStore'];
    commit(RESTORE_CONTENT);
    axios
      .get(`/store/${storeData.id}`)
      .then((res) => {
        if (res.data.success) {
          res.data.data.forEach((item) => {
            if (item.id == id_store) {
              commit(DATA_SAVE, item);
              commit(CATEGORIES_SAVE, item.categories);
            }
          });
        } else {
          errorMessage(res.data.message)
        }
      })
      .catch((error) => errorMessage(error));
  },
  getFeaturedProductsStore({ commit }, id_store) {
    const storeData = store.getters['login/dataStore'];
    axios
      .get(`/store/${storeData.id}/business/${id_store}/featured`)
      .then((res) => {
        if (res.data.success) {
          commit(FEATURED_PRODUCTS, res.data.data);
        } else {
          errorMessage(res.data.message)
        }
      })
      .catch((error) => errorMessage(error));
  },
  getProductsByCategory({ commit }, data) {
    const storeData = store.getters['login/dataStore'];
    axios
      .get(
        `/store/${storeData.id}/business/${data.id_store}/category/${data.id_category}/products`
      )
      .then((res) => {
        if (res.data.success) {
          commit(CATEGORY_PRODUCTS, res.data.data);
        } else {
          errorMessage(res.data.message)
        }
      })
      .catch((error) => errorMessage(error));
  },
  addProductToCart({ commit }, data) {
    const storeData = store.getters['login/dataStore'];
    axios
      .post(
        `/store/${storeData.id}/business/${data.id_business}/car/${data.id_product}`,
        {
          quantity: data.quantity,
          comments: data.comments,
          product: data.id_product,
        }
      )
      .then((res) => {
        if (res.data.success) {
          showMessage(res.data.message);
        } else {
          errorMessage(res.data.message);
        }
      })
      .catch((error) => errorMessage(error));
  },
  async getCartProducts({ commit }) {
    loaderStart();
    const storeData = store.getters['login/dataStore'];
    axios
      .get(
        `/store/${storeData.id}/car`
      )
      .then((res) => {
        if (res.data.success) {
          loaderDone();
          commit(CART_ITEMS_GET, res.data.data);
        } else {
          loaderDone();
          errorMessage(res.data.message)
        }
      })
      .catch((error) => {
        loaderDone();
        errorMessage(error)
      });
  },
  async deleteCartItem({ dispatch }, itemInfo) {
    try {
      loaderStart();
      const storeData = store.getters['login/dataStore'];
      const deletedItem = await axios.post(
        `/store/${storeData.id}/business/${itemInfo.business}/car/${itemInfo.productID}`,
        {
          quantity: 0,
          product: itemInfo.productID,
        }
      );
      if (deletedItem.data.success) {
        await dispatch("getCartProducts");
      } else {
        errorMessage(deletedItem.data.message)
      }
    } catch (error) {
      errorMessage("Error al quitar el producto del carrito");
    } finally {
      loaderDone();
    }
  },
  async updateCartQuantity({ dispatch }, itemInfo) {
    try {
      loaderStart();
      const storeData = store.getters['login/dataStore'];
      const updatedQuantity = await axios.post(
        `/store/${storeData.id}/business/${itemInfo.business}/car/${itemInfo.productID}`,
        {
          quantity: itemInfo.quantity,
          product: itemInfo.productID,
        }
      );
      if (updatedQuantity.data.success) {
        await dispatch("getCartProducts");
      } else {
        errorMessage(updatedQuantity.data.message)
      }
    } catch (error) {
      errorMessage("Error al actualizar el producto del carrito");
    } finally {
      loaderDone();
    }
  },
  getProductsSearchByBusiness({commit}, data) {
    const storeData = store.getters['login/dataStore'];
    if (data.search) {
      axios.get(`/store/${storeData.id}/business/${data.id_store}/search/${data.search}`)
      .then(res => {
        if (res.data.success) {
          commit(RESULTS_BUSINESS_SAVE, res.data.data);
        } else {
          commit(RESULTS_BUSINESS_SAVE, []);
        }
      })
      .catch(error => commit(RESULTS_BUSINESS_SAVE, []))
    } else {
      commit(RESULTS_BUSINESS_SAVE, [])
    }
  }
};
