import {
  DATA_SAVE,
  CATEGORIES_SAVE,
  FEATURED_PRODUCTS,
  RESTORE_CONTENT,
  CATEGORY_PRODUCTS,
  CART_ITEMS_GET,
  RESULTS_BUSINESS_SAVE
} from "./mutation-types";

export default {
  [DATA_SAVE](state, data) {
    state.data = data;
  },
  [CATEGORIES_SAVE](state, categories) {
    state.categories = categories;
  },
  [FEATURED_PRODUCTS](state, data) {
    const arr = [];
    arr.push({
      type: "carousel",
      to: "featuredProducts",
      title: "Productos Destacados",
      data: data,
    });
    state.principalContent = [...state.principalContent, ...arr];
    state.featuredProducts = data;
  },
  [CATEGORY_PRODUCTS](state, data) {
    state.categoryProducts = data;
  },
  [RESTORE_CONTENT](state) {
    state.data = {};
    state.categories = [];
    state.principalContent = [];
  },
  [CART_ITEMS_GET](state, cartItems) {
    state.cartItems = cartItems;
  },
  [RESULTS_BUSINESS_SAVE](state, data) {
    state.resultsSearchBusiness = data
  }
};
