<template>
  <v-navigation-drawer
    width="320"
    fixed
    right
    v-model="shoppingCartDrawerValue"
    clipped
    temporary
  >
    <div class="ma-4">
      <corriendito-cart @clickedShoppingCart="closeShoppingCart" ></corriendito-cart>
    </div>

  </v-navigation-drawer>
</template>

<script>
import ShoppingCart from "@/components/Cart/ShoppingCart";
import { mapActions } from "vuex";
import { errorMessage } from "@/services/MessageService";
import { loaderDone, loaderStart } from "@/services/LoaderService";
export default {
  components: {
    CorrienditoCart: ShoppingCart
  },
  data() {
    return {
      shoppingCartDrawerValue: null
    };
  },
  watch: {
    shoppingCartDrawerValue(newVal) {
      if (newVal) {
        try {
          loaderStart();
          this.getCartProducts();
        } catch (error) {
          errorMessage("Ha ocurrido un error al consultar el carrito");
        } finally {
          loaderDone();
        }
      }
    }
  },
  methods: {
    ...mapActions("products/stores", ["getCartProducts"]),
    closeShoppingCart() {
      this.$emit("clickedShoppingCart") 
    }
  }
};
</script>