// :: Dependencies
import axios from '@/config/axios-auth'
import {
  GET_NOTIFICATIONS
} from "./mutation-types";
import { loaderDone, loaderStart } from "@/services/LoaderService";
import { errorMessage } from "@/services/MessageService";

export default {
  // :: getUserNotifications
  // :: Performs get notifications
  // :: Custom callable function /api/user/notifications
  async getUserNotifications({ commit }) {
    loaderStart();
    try {
      const notificationList = await axios.request({
        url: '/user/notifications',
        method: 'get'
      });
      if (notificationList.data.success) {
        commit(GET_NOTIFICATIONS, notificationList.data);
      } else{
        errorMessage(notificationList.data.message)
      }
    } catch (error) {
      errorMessage("No se pudo obtener las notificaciones de usuario en este momento " + error.code);
    } finally {
      loaderDone();
    }
  }

};