import {
  GET_ORDERS,
  GET_ORDER_DETAIL,
  SET_ORDER_DATA
  } from "./mutation-types";
  
  export default {
    [GET_ORDERS](state, dataOrders) {
      state.myOrders = dataOrders;
    },
    [GET_ORDER_DETAIL](state, dataOrder) {
      state.orderDetail = dataOrder;
    },
    [SET_ORDER_DATA](state, data) {
      state.orderData = data;
    },
  };