// :: Dependencies
import axios from '../../config/axios-auth'
import store from "@/store";
import { loaderDone, loaderStart } from "@/services/LoaderService";
import {
  STORES_SAVE,
  MISSION_SAVE,
  RESULTS_SAVE
} from "./mutation-types";
import { errorMessage } from '../../services/MessageService';


export default {
  // :: getVerificationCodeAction
  // :: Performs a send verification code 
  // :: Custom callable function /api/start
  getStoresById({ commit }, id_store ) {
    axios.get(`/store/${id_store}`)
      .then(res => {
        if (res.data.success) {
          commit(STORES_SAVE, res.data.data);
        } else {
          errorMessage(res.data.message)
        }
      })
      .catch(error => errorMessage(error))
  },
  getMissionsByStore({ commit }, id_store) {
    axios.get(`/store/${id_store}/missions`)
      .then(res => {
        if (res.data.success) {
          commit(MISSION_SAVE, res.data.data);
        } else {
          errorMessage(res.data.message)
        }
      })
      .catch(error => errorMessage(error))
      .finally( loaderDone() )
  },
  getProductsByStoreSearch({ commit }, termToSearch) {
    const storeData = store.getters['login/dataStore'];
    if (termToSearch) {
      axios.get(`/store/${storeData.id}/search/${termToSearch}`)
      .then(res => {
        if (res.data.success) {
          commit(RESULTS_SAVE, res.data.data);
        } else {
          commit(RESULTS_SAVE, []);
        }
      })
      .catch(error => commit(RESULTS_SAVE, []))
    } else {
      commit(RESULTS_SAVE, [])
    }
    
  }
};
