<template>
  <v-container fluid>
    <v-row
      align="center"
      justify="end"
      class="pr-4"
    >
      <v-btn
        icon
        @click="closeShoppingCart"
      >
        <v-icon dark>mdi-close-circle</v-icon>
      </v-btn>
    </v-row>
    <template v-if="!emptyCart">
      <img
        src="@/assets/iconsa/ic_logo_header.png"
        alt="Logo corriendito"
        class="logo-cart"
      />
      <v-row no-gutters>
        <v-col
          cols="12"
          class="text-center title"
        >Productos en tu carrito</v-col>
      </v-row>
      <v-card
        v-for="item in cartItems"
        :key="item.id"
        outlined
        class="mx-auto my-12"
        max-width="374"
      >
        <v-img
          class="cart-item__image"
          contain
          max-height="200"
          :src="item.image"
        ></v-img>
        <v-divider class="mx-0"></v-divider>
        <v-card-title>{{item.name}}</v-card-title>

        <v-card-text>
          <div class="my-4 title">Precio: ${{itemPrice(item.price, item.car.quantity)}}</div>
          <v-row no-gutters>
            <v-col
              cols="3"
              class="pa-2 text-center"
            >
              <v-btn
                outlined
                small
                fab
                color="primary"
                :disabled="item.car.quantity == 1"
                @click.prevent="updateCartQuantity({business: item.car.business_id, productID: item.car.product_id, quantity: item.car.quantity - 1})"
              >
                <v-icon>remove</v-icon>
              </v-btn>
            </v-col>
            <v-col
              cols="6"
              class="pa-2 text-center title"
            >{{item.car.quantity}}</v-col>
            <v-col
              cols="3"
              class="pa-2 tex-center"
            >
              <v-btn
                outlined
                small
                fab
                color="primary"
                @click.prevent="updateCartQuantity({business: item.car.business_id, productID: item.car.product_id, quantity: item.car.quantity + 1})"
              >
                <v-icon>add</v-icon>
              </v-btn>
            </v-col>
          </v-row>
          <div>{{item.description}}</div>
        </v-card-text>

        <v-divider class="mx-4"></v-divider>

        <v-container fluid>
          <v-row no-gutters>
            <v-col
              cols="6"
              class="text-center"
            >
              <v-btn
                color="error lighten-2"
                outlined
                class="pa-1"
                @click="deleteCartItem({business: item.car.business_id, productID: item.car.product_id})"
              >
                <v-icon dense>delete</v-icon>Eliminar
              </v-btn>
            </v-col>
          </v-row>
        </v-container>
      </v-card>
    </template>
    <template v-else>
      <img
        src="@/assets/iconsa/ic_logo_header.png"
        alt="Logo corriendito"
        class="logo-cart"
      />
      <p class="text-data-lg">Tu carrito esta vacío</p>
      <p class="text-data-sm">Te invitamos a volver a nuestras tiendas y agregar productos a tu carrito.</p>
      <v-btn
        block
        large
        color="primary"
      >Seguir comprando</v-btn>
    </template>
    <div class="footer_cart">
      <v-row
        no-gutters
        v-if="!emptyCart"
      >
        <v-col
          cols="6"
          class="title"
        >Subtotal :</v-col>
        <v-col
          cols="6"
          class="title text-right"
        >${{totalPrice}}</v-col>
        <v-col
          cols="12"
          class="text-center"
        >
          <v-btn
            @click="goToPayOrder"
            block
            color="primary"
          >Pagar</v-btn>
        </v-col>
      </v-row>
    </div>
  </v-container>
</template>
<script>
import { mapState, mapActions, mapGetters } from "vuex";
import { errorMessage, showMessage } from "@/services/MessageService";
export default {
  components: {},
  computed: {},
  data() {
    return {
      items: [
        { title: "Mis datos", to: "home" }, //User profile
        { title: "Notificaciones", to: "home" } //notificaciones
      ],
      total: 0
    };
  },
  computed: {
    ...mapState("products/stores", ["cartItems"]),
    ...mapGetters("login", ["dataStore"]),
    emptyCart() {
      if (this.cartItems.length > 0) {
        return false;
      } else {
        return true;
      }
    },
    totalPrice() {
      this.total = 0;
      this.cartItems.forEach(item => {
        this.total = item.price * item.car.quantity + this.total;
      });
      return this.total.toFixed(2);
    }
  },
  methods: {
    ...mapActions("products/stores", ["deleteCartItem", "updateCartQuantity"]),
    ...mapActions("checkout", ["setSubtotalPrice"]),
    itemPrice(price, quantity) {
      const itemPrice = price * quantity;
      return itemPrice.toFixed(2);
    },
    closeShoppingCart() {
      this.$emit("clickedShoppingCart");
    },
    goToPayOrder() {
      var currentDate = new Date();
      const openStore = this.dataStore.opening_time.split(":");
      const closeStore = this.dataStore.closing_time.split(":")
      var openingTime = new Date();
      openingTime.setHours(openStore[0], openStore[1], "00"); // 5.30 pm
      var closingTime = new Date();
      closingTime.setHours(closeStore[0], closeStore[1], "00"); // 6.30 pm

      if (
        currentDate >= openingTime && currentDate < closingTime
      ) {
        if (this.totalPrice >= this.dataStore.minimum_purchase) {
          this.closeShoppingCart();
          this.setSubtotalPrice( this.total)
          this.$router.push({ name: "checkout" });
        } else {
          showMessage(`El minimo de compra es $ ${this.dataStore.minimum_purchase}`);
        }
      } else {
        errorMessage({
          message: `La tienda se encuentra cerrada, intentalo mañana a partir ${this.dataStore.opening_time}`
        });
      }
    }
  }
};
</script>
