  padding: 0;
  margin: 0;
  height: 10px;
  width: 10px;
  right: 4px;
  top: 4px;
  position: absolute;
}
.footer_cart {
  position: sticky;
  bottom: 0px;
  background-color: white;
  width: 100%;
  height: 80px;
}
</style>