<template>
  <v-app-bar color="white">
    <div class="row-toolbar d-flex flex-grow-1 justify-space-around align-center">
      <!-- <v-app-bar-nav-icon @click.stop="toggleSidebar" color="primary"></v-app-bar-nav-icon> -->
      <v-toolbar-title class="toolbar" @click.prevent="navigateToHome">
        <img class="logo" src="@/assets/iconsa/ic_logo_header.png" alt="Logo corriendito" />
      </v-toolbar-title>
      <v-spacer></v-spacer>

      <v-dialog v-if="$route.name !== 'checkout'" v-model="addressListDialog" scrollable max-width="400px">
      <template v-slot:activator="{ on, attrs }">
        <v-btn large class="btn-address d-none d-lg-flex d-xl-none" v-bind="attrs" v-on="on" @click="componentKeyToReload=!componentKeyToReload">
          <v-icon color="primary">mdi-map-marker-outline</v-icon>{{address}}
          <v-icon color="black">mdi-chevron-down</v-icon>
        </v-btn>

        <v-btn icon class="btn-address d-lg-none d-xl-flex" v-bind="attrs" v-on="on" @click="componentKeyToReload=!componentKeyToReload">
          <v-icon color="primary">mdi-map-marker-outline</v-icon>
        </v-btn>
      </template>
      <AddresList :key="componentKeyToReload" @closeAddressListDialog="dismissAddressListDialog"></AddresList>
      </v-dialog>
      
      <v-btn v-if="$route.name !== 'checkout'" icon color="primary" @click.stop="toggleShoppingCart">
        <v-icon>mdi-cart-outline</v-icon>
      </v-btn>
      <v-menu v-if="$route.name !== 'checkout'" style="top:60px !important" offset-y>
        <template v-slot:activator="{ on }">
          <v-btn icon color="primary" v-on="on">
            <v-icon x-large>mdi-account-circle-outline</v-icon>
          </v-btn>
        </template>
        <menu-user></menu-user>
      </v-menu>
    </div>
  </v-app-bar>
</template>
<script>
import { mapState, mapActions } from "vuex";
import menu from "@/components/User/UserMenu";
import addressList from "@/components/User/AddressList";
export default {
  components: {
    MenuUser: menu,
    AddresList: addressList
  },
  computed: {
    ...mapState("login", ["data", "flagLogin", "addressCount", "auxOneAddress"])
  },
  async mounted() {
    this.address = this.data.addressSelected && this.data.addressSelected.addressName.length > 0 ? this.data.addressSelected.addressName : "Dirección";
  },
  data() {
    return {
      addressListDialog: null,
      address: "",
      componentKeyToReload: 0
    };
  },
  methods: {
    ...mapActions("login", ["updateFlagLogin"]),
    toggleSidebar() {
      this.$emit("clickedSidebar");
    },
    toggleShoppingCart() {
      this.$emit("clickedShoppingCart");
    },
    dismissAddressListDialog(value){
      this.address = this.data.addressSelected && this.data.addressSelected.addressName.length > 0 ? this.data.addressSelected.addressName : "Dirección";
      this.addressListDialog = value;
    },
    navigateToHome() {
      this.$router.push({ name: "home" }).catch(err => err);
    }
  },
  watch:{
    //observe addressCount value
    async addressCount(val){
      //open addressListDialog when addressCount change its value
        if (val > 1 && this.flagLogin == false){
          this.addressListDialog = true;
          await this.updateFlagLogin(true); //change value in order to do once
        }
    },
    //observe auxOneAddress value
    auxOneAddress(val){
      //update address title button when there is one address
      this.address = this.data.addressSelected && this.data.addressSelected.addressName.length > 0 ? this.data.addressSelected.addressName : "Dirección";
    }
  }
};
</script>
<style>
.row-toolbar {
  max-width: 90rem;
  margin: 0 auto;
}
.toolbar {
  display: inline-table;
}
.logo {
  margin-left: 0px;
  margin-top: 9px;
  padding: 0;
  height: 56px;
  cursor: pointer;
}
.search {
  width: 50%;
}
.btn-address {
  background-color: white !important;
  border-radius: 50px !important;
  text-transform: unset;
}
</style>