// :: Dependencies
import axios from '@/config/axios-auth'
import {
  GET_ADDRESS_LIST
} from "./mutation-types";
import { loaderDone, loaderStart } from "@/services/LoaderService";
import { errorMessage } from "@/services/MessageService";

export default {
  // :: getUserAddressList
  // :: Performs get address list
  // :: Custom callable function /api/address
  async getUserAddressList({ commit }) {
    loaderStart();
    try {
        const addressList = await axios.request({
            url: '/address',
            method: 'get'
        });
        if (addressList.data.success) {
          commit(GET_ADDRESS_LIST, addressList.data);
        } else{
          errorMessage(addressList.data.message)
        }
      } catch (error) {
        errorMessage("No se pudo obtener las direcciones de usuario en este momento " + error.code);
      } finally {
        loaderDone();
      }
  }
};