import Vue from "vue";
import Router from "vue-router";
import store from "@/store";

// :: Views imported
const Login  = () => import( "@/views/Login/Login");
const PhoneNumber = () => import ("@/components/Login/PhoneNumber");
const VerificationCode = () => import ("@/components/Login/VerificationCode");
const Password = () => import ("@/components/Login/Password");
const ResetPassword = () => import ("@/components/Login/ResetPassword");
const Home = () => import ("@/views/Home/Home");
const Register = () => import ( "@/views/Register/Register");
const Store = () => import ( "@/views/Products/Store");
const StoreContent = () => import ("@/components/Products/StoreContent");
const FeaturedProducts = () => import ("@/components/Products/FeaturedProducts");
const CategoryProducts = () => import ("@/components/Products/Category");
const SearchInStore = () => import ("@/components/Products/Results");
const PreguntasFrecuentes = () => import ( "@/views/FAQ/faq");
const terminos = () => import ("@/views/Terminos/terminos");
const orders = () => import ("@/views/Orders/orders");
const orderDetails = () => import ("@/views/Orders/OrderDetails");
const ResultsStore = () => import ("@/views/Products/ResultsSearch")
const Checkout = () => import ("@/views/Checkout/Checkout")
/**
 * Address register view import
 * This is the proper way to declare a dynamic loading component to use vue lazy loading
 */
const addressRegister = () => import("@/views/User/AddressRegister");
const Notifications = () => import("@/views/User/Notifications");
const Profile = () => import("@/views/User/Profile.vue");
const Favorites = () => import("@/views/Favorites/Favorites.vue")
const routes = [
  {
    path: "/",
    name: "home",
    component: Home,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/store/:id",
    component: Store,
    meta: {
      requiresAuth: true,
    },
    children: [
      {
        path: "",
        name: "store",
        component: StoreContent,
        meta: {
          requiresAuth: true,
        },
      },
      {
        path: "featuredProducts",
        name: "featuredProducts",
        component: FeaturedProducts,
        meta: {
          requiresAuth: true,
        },
      },
      {
        path: "category",
        name: "category",
        component: CategoryProducts,
        props: true,
        meta: {
          requiresAuth: true,
        },
      },
      {
        path: "store-results",
        name: "storeResults",
        component: SearchInStore,
        props: true,
        meta: {
          requiresAuth: true,
        },
      }
    ]
  },
  {
    path: "/signin",
    name: "login",
    component: Login,
    children: [
      {
        path: "phoneNumber",
        name: "phoneNumber",
        component: PhoneNumber
      },
      {
        path: "verificationCode",
        name: "verificationCode",
        component: VerificationCode
      },
      {
        path: "password",
        name: "password",
        component: Password
      },
      {
        path: "resetPassword",
        name: "resetPassword",
        component: ResetPassword
      }
    ]
  },
  {
    path: "/register",
    name: "register",
    component: Register,
  },
  {
    path: "/PreguntasFrecuentes",
    name: "PreguntasFrecuentes",
    component: PreguntasFrecuentes
  },
  {
    path: "/terminos",
    name: "terminos",
    component: terminos

  },
  {
    path: "/notificaciones",
    name: "notifications",
    component: Notifications,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/perfil",
    name: "profile",
    component: Profile,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/registro-de-dirección",
    name: "addressRegister",
    component: addressRegister,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/resultados",
    name: "results",
    component: ResultsStore,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/favoritos",
    name: "favorites",
    component: Favorites,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/orders",
    name: "orders",
    component: orders,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/orders/:id",
    name: "orderDetail",
    component: orderDetails,
    meta: {
      requiresAuth: true,
    }
  },
  {
    path: "/checkout",
    name: "checkout",
    component: Checkout,
    meta: {
      requiresAuth: true
    }
  }
  //   {
  //     path: "*",
  //     name: "404",
  //     component: NotFound
  //   }
];

const router = new Router({
  routes,
  mode: "history",
  base: process.env.BASE_URL,
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 };
  }
});

// :: Auth check before each route visit
router.beforeEach((to, from, next) => {
  const requiresAuth = to.matched.some(({ meta }) => meta.requiresAuth);

  const logged = store.getters["login/isLogged"];
  const verificationCode = store.getters["login/verificationCode"];

  if (to.name === "login") {
    return next("/signin/phoneNumber");
  }

  if (to.name === "register" && verificationCode === "") {
    return next("/signin/phoneNumber");
  }
  // if the user is not authenticated, `next` is called twice

  // :: If the path requires the user be authenticated, redirect to login
  if (requiresAuth && !logged) {
    return next("/signin/phoneNumber");
  }

  next();
});

Vue.use(Router);

export default router;
