import axios from '@/config/axios-auth'
import { errorMessage } from "@/services/MessageService";
import {
  GET_PREGUNTAS
} from "./mutation-types";

export default {
  async getFaqs({ commit }) {
 
    axios.request({
        url: '/faqs',
        method: 'get'
      }).then(response => {
        if (response.data.success) {
          commit(GET_PREGUNTAS, response.data);
        } else {
          errorMessage('error api faqs');
        }
      })
      .catch(error => errorMessage(error))
      
    }
}