import {
  STORE_SAVE,
  STORES_SAVE,
  MISSION_SAVE,
  RESULTS_SAVE
} from "./mutation-types";

export default {
  [STORE_SAVE](state, dataStores) {
    state.dataStoreByLocation = dataStores
  },
  [STORES_SAVE](state, dataStores) {
    state.dataStoresByStore = dataStores
  },
  [MISSION_SAVE](state, dataMissions) {
    state.dataMissionsByStore = dataMissions
  },
  [RESULTS_SAVE](state, dataResults) {
    // const arr = []

    if (dataResults.length > 0) {
      // dataResults.forEach(item => item.products.forEach(product => arr.push(product)))
      state.dataResultsSearchByStore = dataResults
    } else {
      state.dataResultsSearchByStore = []
    }
  }
};
