// :: Dependencies
import axios from '@/config/axios-auth'
import {
    GET_USER_PROFILE,
    UPDATE_USER_PROFILE
} from "./mutation-types";
import { loaderDone, loaderStart } from "@/services/LoaderService";
import { errorMessage, showMessage } from "@/services/MessageService";

export default {
  // :: getUserProfile
  // :: Performs get user profile
  // :: Custom callable function /api/profile
  async getUserProfile({ commit }) {
    loaderStart();
    try {
      const profile = await axios.request({
        url: '/profile',
        method: 'get'
      });
      if (profile.data.success) {
        commit(GET_USER_PROFILE, profile.data);
      } else {
        errorMessage(profile.data.message)
      }
    } catch (error) {
      errorMessage("No se pudo obtener el perfil de usuario en este momento " + error.code);
    } finally {
      loaderDone();
    }
  },
  // :: updateUserProfile
  // :: Performs post user profile
  // :: Custom callable function /api/profile
  async updateUserProfile({ commit }, newUserProfileData) {
    loaderStart();
    const { profile_picture, name, email, birthday, gender } = newUserProfileData;
    try {
      const profileUpdated = await axios.post('/profile', {
        name: name,
        email: email,
        birthday: birthday,
        gender: gender,
        profile_picture: profile_picture
      });
      if (profileUpdated.data.success) {
        commit(UPDATE_USER_PROFILE, profileUpdated.data);
        showMessage("Perfil actualizado correctamente");
      } else {
        errorMessage(profileUpdated.data.message)
      }
    } catch (error) {
      errorMessage("No se pudo actualizar el perfil de usuario en este momento " + error.code);
    } finally {
      loaderDone();
    }
  }
};