// :: Dependencies
import axios from "../../config/axios-auth";
import store from "@/store";
import router from "@/config/router";
import { loaderDone, loaderStart } from "@/services/LoaderService";
import { errorMessage, showMessage } from "@/services/MessageService";
import { CHECKOUT_PROCESS, SAVE_PAYMENT_METHODS, SAVE_SUBTOTAL, SAVE_PICKUP_METHODS } from "./mutation-types";
export default {
  async postCheckoutProcess({ commit }, dataPurchase) {
    try {
      loaderStart()
      const storeData = store.getters['login/dataStore'];
      const checkout = await axios.post(`/store/${storeData.id}/car/process`, {
        address: dataPurchase.address,
        payment_method: dataPurchase.payment_method,
        cash_amount: dataPurchase.cash_amount,
        pickup_time_id: dataPurchase.pickup_time_id,
        delivery_type_id: dataPurchase.delivery_type_id,
        delivery_distance: dataPurchase.delivery_distance,
        delivery_price: dataPurchase.delivery_price,
        comments: dataPurchase.comments

      });
      if (checkout.data.success) {
        loaderDone()
        showMessage(checkout.data.message)
        router.push({name: 'home'});
      } else {
        loaderDone()
        errorMessage(checkout.data.message)
        router.push({name: 'home'});
      }
    } catch (error) {
      loaderDone()
      errorMessage("Ocurrió un error al realizar el proceso de compra " + error.code);
    }
  },
  async getPaymentMethods ({commit}){
    try {
      loaderStart()
      const paymentMethods = await axios.get(`/paymentMethods`, {
        content_type: "application/x-www-form-urlencoded",
      });
      if (paymentMethods.data.success) {
        loaderDone()
        commit(SAVE_PAYMENT_METHODS, paymentMethods.data.data);
      } else {
        loaderDone()
        errorMessage(paymentMethods.data.message);
      }
    } catch (error) {
      loaderDone()
      errorMessage("Ocurrió un error al consultar los métodos de pago " + error.code);
    }
  },
  setSubtotalPrice({commit}, subtotal) {
    commit(SAVE_SUBTOTAL, subtotal)
  },
  async getTimeOptions ({commit}){
    try {
      loaderStart()
      const storeData = store.getters['login/dataStore'];
      const pickupMethods = await axios.get(`/store/${storeData.id}/pickup/times`, {
        content_type: "application/x-www-form-urlencoded",
      });
      if (pickupMethods.data.success) {
        loaderDone()
        commit(SAVE_PICKUP_METHODS, pickupMethods.data.data);
      } else {
        loaderDone()
        errorMessage(pickupMethods.data.message);
      }
    } catch (error) {
      loaderDone()
      errorMessage("Ocurrió un error al consultar los horarios de entrega " + error.code);
    }
  }
};
