// :: Dependencies
import axios from '@/config/axios-auth'
import store from "@/store";
import {
  GET_ORDERS,
  GET_ORDER_DETAIL,
  SET_ORDER_DATA
} from "./mutation-types";
import { loaderDone, loaderStart } from "@/services/LoaderService";
import { errorMessage } from "@/services/MessageService";

export default {
  // :: getUserOrders
  // :: Performs get orders
  // :: Custom callable function /api/sales
  async getUserOrders({ commit }) {
    loaderStart();
    try {
      const ordersList = await axios.request({
        url: '/sales',
        method: 'get'
      });
      if (ordersList.data.success) {
        commit(GET_ORDERS, ordersList.data.data);
      } else{
        errorMessage(ordersList.data.message)
      }
    } catch (error) {
      errorMessage("No se pudo obtener las ordenes del usuario en este momento " + error.code);
    } finally {
      loaderDone();
    }
  },
  async getOrderData({ commit }, id_order) {
    loaderStart();
    try {
      const storeData = store.getters['login/dataStore'];
      const orderDetail = await axios.request({
        url: `/store/${storeData.id}/sales/${id_order}/products`,
        method: 'get'
      });
      if (orderDetail.data.success) {
        commit(GET_ORDER_DETAIL, orderDetail.data.data);
      } else{
        errorMessage(orderDetail.data.message)
      }
    } catch (error) {
      errorMessage("No se pudo obtener el detalle de la orden en este momento " + error.code);
    } finally {
      loaderDone();
    }
  },
  async setOrderData({ commit }, data) {
      commit(SET_ORDER_DATA, data);
  }

};